.drum-pad {
  display: block;
  font: bold 9pt arial;
  text-decoration: none;
  font: 18px/48px Helvetica, serif;
  text-transform: uppercase;
  text-align: center;
  color: #666;
  width: 44px;
  height: 41px;
  margin: 5px;
  background: #eff0f2;
  -moz-border-radius: 4px;
  border-radius: 4px;
  border-top: 1px solid #f5f5f5;
  -webkit-box-shadow: inset 0 0 25px #e8e8e8, 0 1px 0 #c3c3c3, 0 2px 0 #c9c9c9,
    0 2px 3px #333;
  -moz-box-shadow: inset 0 0 25px #e8e8e8, 0 1px 0 #c3c3c3, 0 2px 0 #c9c9c9,
    0 2px 3px #333;
  box-shadow: inset 0 0 25px #e8e8e8, 0 1px 0 #c3c3c3, 0 2px 0 #c9c9c9,
    0 2px 3px #333;
  text-shadow: 0px 1px 0px #f5f5f5;
}

.drum-pad:active {
  color: #888;
  background: #ebeced;
  margin: 7px 5px 3px;
  -webkit-box-shadow: inset 0 0 25px #ddd, 0 0 3px #333;
  -moz-box-shadow: inset 0 0 25px #ddd, 0 0 3px #333;
  box-shadow: inset 0 0 25px #ddd, 0 0 3px #333;
  border-top: 1px solid #eee;
}

button,
button:hover {
  background-color: rgba(0, 0, 0, 0);
  text-decoration: none;
  border: none;
  width: 35px;
  height: 30px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: inherit;
}

#drum-machine {
  background-color: rgb(224, 224, 224);
  border-style: none;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  align-items: center;
  height: 50vh;
  width: 50vw;
  margin: 100px;
}

#display {
  background-color: #ccc;
  text-decoration: none;
  border: none;
  width: 20%;
  height: 30%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #a3a3a3;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 10px;
}

#container {
  width: 180px;
  display: flex;
  flex-flow: row wrap;
}

p {
  margin: 0px;
}

body {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  margin: auto;
}

a {
  text-decoration: none;
  color: #2a4e29;
}

a:hover {
  color: #8a8a8a;;
}

a:hover .code {
  color: #cc9f22;
}

.code {
  color: #ffffff;
  transition: color 0.3s ease-out;
}

footer {
  padding: 14px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #a3a3a3;
}
